.feedback-form {
  max-width: 480px;
  background-color: #db7093;
  border: 3px solid #855a5a;
  border-radius: 18px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  background-color: #ffa07a;
  border: 3px solid #665656;
  border-radius: 18px;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  background-color: #bdb76b;
  border: 3px solid #665656;
  border-radius: 18px;
  padding: 8px;
}

textarea {
  resize: none;
}

